import { Form, Input, Button, message, Popconfirm } from 'antd';
import React, { Component } from 'react';
import fetchApi from '../../api/callApi';
import { resetPassword } from '../../api/OAuth';

interface formType {
    current: string
}

const layout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 16 },
};
const tailLayout = {
    wrapperCol: { offset: 4, span: 16 },
};

const validateMessages = {
    required: "",
};

class ResetPwd extends Component<any, any> {
    formRef = React.createRef();

    onFinish = (values: any) => {
        // console.log(values.newpw1);
        // console.log(values.newpw2);
        if (values.newpw1 === values.newpw2) {
            // 
            message.warning("")
            const { apiPath, request } = resetPassword(values.newpw1, values.newpw2);
            fetchApi(apiPath, request)
                .then(res => res.json())
                .then(data => {
                    console.log(data)
                })
        } else {
            message.error("")
        }
    };

    render() {
        return (
            <div className="reset-pwd">
                <Form {...layout} className="reset-pwd-form card-shadow" name="control-ref" onFinish={this.onFinish} validateMessages={validateMessages}>
                    <Form.Item name="newpw1" label="" rules={[{ required: true }]}>
                        <Input allowClear />
                    </Form.Item>
                    <Form.Item name="newpw2" label="" rules={[{ required: true }]}>
                        <Input allowClear />
                    </Form.Item>
                    <p className="reset-pwd-tip">  8 </p>
                    <Form.Item {...tailLayout}>
                        <Button type="primary" htmlType="submit"></Button>
                    </Form.Item>
                </Form>
            </div>
        );
    }
}

export default ResetPwd