import React, { Component } from 'react';
import { Button } from 'antd';
import { Link } from 'react-router-dom';

export interface Props {

}

export interface State {

}

class Meeting extends Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {};
    }
    render() {
        return (
            <div>
                <div className="work-title"></div>
                <div className="meeting-card card-shadow">
                    <p className="meeting-card-title"> </p>
                    <p className="meeting-card-time">2020-07-08 14:20</p>
                    <div className="meeting-card-detail">
                        <p></p>
                        <p> </p>
                        <p> </p>
                        <p> </p>
                    </div>
                    <p className="meeting-card-desc"></p>
                    <div className="meeting-card-btns">
                        <Link to={`/Work/Sign?id=${0}`}>
                            <Button className="meeting-card-btn-sign card-shadow"></Button>
                        </Link>
                        <Link to={`/Work/Leave?id=${0}`}>
                            <Button className="meeting-card-btn-leave card-shadow"></Button>
                        </Link>
                    </div>
                </div>
            </div >
        );
    }
}

export default Meeting;