import config from '../site-config.json';

const lack = "site-config.json";
const lackTip = "";

export const org = () => {
    if (config.org) {
        return config.org
    } else {
        console.log(lack + "org<string>");
        return lackTip
    }
}

export const orgInShort = () => {
    if (config.orgInShort) {
        return config.orgInShort
    } else {
        console.log(lack + "orgInShort<string>");
        return lackTip
    }
}

export const siteTitle = () => {
    if (config.title) {
        return config.title
    } else {
        console.log(lack + "title<string>");
        return lackTip
    }
}