import React, { Component } from 'react';
import { Input, Form } from 'antd';
import { Link } from 'react-router-dom';
import MeetingRecord from './MeetingRecord';
const auth = sessionStorage.getItem("auth");
const { Search } = Input;


export interface Props {

}

export interface State {
    status: number
}

class MeetingHistory extends Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            status: 1
        };
    }

    renderStatus = (status: number) => {
        switch (status) {
            case 1:
                return <span>
                    <span style={{ color: "#89C997", fontWeight: "bold" }}></span>
                    <span style={{ color: "#DCDCDC", fontWeight: "bold" }}></span>
                    <span style={{ color: "#DCDCDC", fontWeight: "bold" }}></span>
                </span>
                break;
            case 2:
                return <span>
                    <span style={{ color: "#DCDCDC", fontWeight: "bold" }}></span>
                    <span style={{ color: "#F8B551", fontWeight: "bold" }}></span>
                    <span style={{ color: "#DCDCDC", fontWeight: "bold" }}></span>
                </span>
                break;
            case 3:
                return <span>
                    <span style={{ color: "#DCDCDC", fontWeight: "bold" }}></span>
                    <span style={{ color: "#DCDCDC", fontWeight: "bold" }}></span>
                    <span style={{ color: "#EC6941", fontWeight: "bold" }}></span>
                </span>
                break;
            default:
                break;
        }
    }

    render() {
        return (
            <div>
                <div className="work-title"></div>
                <div className="hismetting-card card-shadow">
                    <div className="hismetting-search">
                        <Search
                            style={{ display: "none" }}
                            // 
                            prefix={" "}
                            placeholder="input search text"
                            onSearch={(value: any) => console.log(value)}
                        />
                    </div>
                    <p className="hismetting-list"><span className="hismetting-list-title"></span>XXX2020N</p>
                    <p className="hismetting-list"><span className="hismetting-list-title"></span>2020.05.09 18:302020.05.09 19:00</p>
                    <p className="hismetting-list"><span className="hismetting-list-title"></span></p>
                    <p className="hismetting-list"><span className="hismetting-list-title"></span>3017005243</p>
                    <p className="hismetting-list"><span className="hismetting-list-title"></span>3017005243</p>
                    <p className="hismetting-list"><span className="hismetting-list-title"></span></p>
                    <p className="hismetting-list"><span className="hismetting-list-title"></span>39 / 50
                    {auth === "2" ?
                            <Link to={`/Work/GroupAttending?id=${0}`} className="mygroup">
                                
                            </Link>
                            : null}
                    </p>
                    <p className="hismetting-list"><span className="hismetting-list-title"></span>{this.renderStatus(this.state.status)}</p>
                </div>
                <MeetingRecord />
            </div>
        );
    }
}

export default MeetingHistory;
